export default {
  name: "SInput",

  props: {
    labelName: {
      type: String,
      required: false
    },
    fieldId: {
      type: String,
      required: false
    },
    cssClass: {
      type: String,
      required: false
    },
    placeholder: {
      type: String,
      default: ""
    },
    value: {
      type: String,
      default: ""
    },
    iconName: {
      type: String
    }
  },
  data() {
    return {
      content: this.value
    };
  },

  methods: {
    updateValue: function(value) {
      this.$emit("input", value);
    }
  },
  watch: {
    value(value) {
      this.onChangeFn(value);
    }
  }
};
